@import url(https://fonts.googleapis.com/css2?family=Manrope&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
    font-family: 'Manrope';
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

#card {
    border: 2px solid white;
    border-radius: 20px;
    color: black;
    width: 80%;
}

.Note {
    padding-bottom: 10px;
}

#cardTitle {
    background-color: rgb(152, 255, 152);
    border-radius: 20px;
}

#cardTitle>.card-title {
    font-size: xx-large;
}

#addNote {
    float: right;
    display: inline;
    font-size: xx-large;
}

@media screen and (max-device-width: 768px) {
    #card {
        width: 90%;
    }
}

@media screen and (max-device-width: 414px) {
    #card {
        width: 100%;
    }
}
#chkBox {
    transform: scale(1.7);
    margin-right: 10px;
    display: inline;
    float: left;
}

.form-inline {
    width: 100%;
}

.form-group {
    width: 100%;
}

.input-group {
    width: 90% !important;
}

.form-control {
    margin: auto;
    margin-left: 2px;
    margin-right: 2px;
    width: 90% !important;
    display: inline;
}

.Button {
    float: right;
}

@media screen and (max-device-width: 768px) {
    .form-control {
        width: 85% !important;
    }
}

@media screen and (max-device-width: 414px) {
    #chkBox {
        transform: scale(1.7);
        margin-top: 12px;
        display: inline;
        float: left;
    }
    .form-control {
        margin: auto;
        width: 80% !important;
        display: inline;
    }
    .Button {
        margin-left: 4px;
        margin-right: 4px;
        display: inline;
    }
}

@media screen and (max-device-width: 320px) {
    .form-control {
        width: 70% !important;
    }
}
